},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"row justify-content-center align-items-center mb-4",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"col-lg-5 col-md-7 col-10 mb-4"},[_c('img',{attrs:{"src":require("@/assets/curso/tema1/6.png")}})]),_c('div',{staticClass:"col-lg-7"},[_c('div',{staticClass:"row justify-content-center align-items-center"},[_c('div',{staticClass:"col-lg-2"},[_c('img',{staticClass:"d-none d-lg-block",attrs:{"src":require("@/assets/curso/tema1/7.svg")}})]),_c('div',{staticClass:"col-lg-10"},[_c('p',{staticClass:"mb-0"},[_vm._v("Este fragmento, inspirado en el libro “Por una pedagogía de la pregunta” de Paulo Freire y Antonio Faundez (2013), resalta que el profesional, en su permanente proceso de aprendizaje y constante inquietud, debe asumir una actitud crítica y reflexiva frente a cualquier proposición. Los autores invitan a “aprender a preguntar”, destacando que todo conocimiento comienza precisamente con una pregunta. La curiosidad, que muchas veces se considera el punto de partida del saber, no es más que una forma de pregunta en sí misma. Esta perspectiva se articula con el pensamiento crítico propuesto por autores como Matthew Lipman, quien señala que la educación debe fomentar la indagación filosófica desde edades tempranas, promoviendo el cuestionamiento como herramienta para el desarrollo del pensamiento reflexivo. Asimismo, John Dewey, desde el pragmatismo, subraya que el aprendizaje significativo surge cuando el individuo enfrenta una situación problemática que lo obliga a pensar, investigar y formular preguntas que orienten su comprensión del mundo. En este sentido, preguntar no solo es el inicio del conocimiento, sino también el motor que impulsa la transformación personal, social y educativa.")])])])])])
},function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"bl1"},[_c('div',{staticClass:"bloque-texto-a color-acento-contenido p-4 p-md-5 mb-5",attrs:{"data-aos":"fade-right"}},[_c('div',{staticClass:"row m-0 align-items-center justify-content-center"},[_c('div',{staticClass:"col-lg-8 mb-0 com"},[_c('div',{staticClass:"bloque-texto-a__texto p-lg-5 p-4"},[_c('p',{staticClass:"mb-4",attrs:{"data-aos":"fade-left"}},[_vm._v("La pregunta, según Freire y Faundez (2013), no se reduce a un mero ejercicio intelectual, sino que es un acto profundamente relacionado con la curiosidad y el deseo de indagar. Este concepto es fundamental, especialmente en contextos académicos y laborales, donde a menudo se tecnifican los discursos, creando un ambiente donde las preguntas simples, o aquellas que parecen obvias, quedan sin respuesta. Este silencio, en muchos casos, es producto de un temor implícito, que genera la deslegitimación de la pregunta como un derecho legítimo del individuo.")]),_c('p',{staticClass:"mb-0",attrs:{"data-aos":"fade-left"}},[_vm._v("Freire y Faundez, al reflexionar sobre qué significa preguntar, proponen una visión enriquecedora: preguntar es vivir la indagación, vivir la curiosidad, y permitir que esa curiosidad se transforme en un proceso continuo de cuestionamiento. Este enfoque no solo promueve una actitud inquisitiva, sino que también invita a los profesionales a admirar otras perspectivas construidas desde su propia inquietud. Así, el proceso de hacer preguntas no debe limitarse a los aspectos superficiales de un tema, sino que debe avanzar desde lo simple hacia lo complejo, permitiendo una comprensión más profunda.")])])]),_c('div',{staticClass:"col-lg-4 col-md-6 mb-0"},[_c('img',{staticClass:"enc img-a",attrs:{"src":require("@/assets/curso/tema1/8.svg"),"alt":""}})])])])])
