ías de observación que auxilien a sistematizar el proceso; así como la duración y frecuencia de los registros; la funcionalidad de estas guías estará sujeta al tipo de observación que se emplee: de laboratorio, participante, no participante, estructurada o no estructurada.")])])])])])
